<template>
  <div class="ForeignLanguageTotalBackground">
    <!--     <input
            type="text"
            class="ForeignLanguageUserNumberInput"
            v-model="ForeignLanguageUserNumber"
            placeholder="请输入学生学号"
          />
          <button  class="ForeignLanguageSeekButton" @click="ForeignLanguageSeekButtonClick">查询</button>
          <button  class="ForeignLanguageEditButton" @click="ForeignLanguageEditButtonClick">编辑</button>
       -->
    <div class="ForeignLanguageTableBackground">
      <table>
        <thead>
          <tr>
            <th style="width: 3%">姓名</th>
            <th style="width: 5%">考试类型</th>
            <th style="width: 4%">证书编号</th>
            <th style="width: 4%">分数</th>
            <th style="width: 4%">获得时间</th>
            <th style="width: 4%">颁发部门</th>
            <th style="width: 5%">照片</th>
            <th style="width: 1%">详情</th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="(studentRecord, index) in ForeignLanguageTable"
            :key="index"
          >
            <td>
              {{ studentRecord.englishExamDetails[0].studentName }}
            </td>
            <td>
              {{ studentRecord.englishExamDetails[0].typeName }}
            </td>
            <td>
              {{ studentRecord.englishExamDetails[0].certificateNumber }}
            </td>
            <td>
              {{ studentRecord.englishExamDetails[0].score }}
            </td>

            <td>
              {{ formatDate(studentRecord.englishExamDetails[0].getTime) }}
            </td>
            <td>
              {{ studentRecord.englishExamDetails[0].issuingDepartment }}
            </td>
            <td
              @click="
                pictureInspectClick(studentRecord.englishExamDetails[0].photo)
              "
            >
              <img
                :src="studentRecord.englishExamDetails[0].photo"
                alt="获奖照片"
                width="100"
                height="30"
              />
            </td>
            <!-- 查看详情 -->
            <td @click="ViewDetails(studentRecord)" class="Details">查看</td>
          </tr>
        </tbody>
      </table>
    </div>

    <div class="ForeignLanguagePagination">
      <el-pagination
        size="small"
        :page-size="pageSize"
        :pager-count="10"
        background
        layout="prev, pager, next"
        :total="total"
        @current-change="handlePageChange"
      />
    </div>

    <div v-if="ifDetails" class="ForeignLanguageDetailsBackground">
      <div class="ForeignLanguageDetailsFormBackground">
        <span class="ForeignLanguageDetailsFormTitle">学生奖项详细信息</span>
        <form class="ForeignLanguageDetailsForm">
          <div class="label-row">
            <label>姓名 : {{ studentDetails[0].studentName }}</label>
            <label>学号 : {{ studentDetails[0].studentNumber }}</label>
          </div>
          <div class="label-row">
            <label>身份证号 : {{ studentDetails[0].idCard }}</label>
          </div>

          <div class="label-row">
            <label>学院 : {{ studentDetails[0].college }}</label>
            <label>专业 : {{ studentDetails[0].major }}</label>
          </div>

          <div v-for="(data, index) in studentDetails" :key="index">
            <div class="label-row">
              <label>准考证号 : {{ data.examinationCertificateNumber }}</label>
            </div>
            <div class="label-row">
              <label>考试类型 : {{ data.typeName }}</label>
              <label>证书编号 : {{ data.certificateNumber }}</label>
            </div>
            <div class="label-row">
              <label>考试时间 : {{ data.getTime }}</label>
              <label>总分 : {{ data.score }}</label>
            </div>
            <div class="label-row">
              <label>颁发部门 : {{ data.issuingDepartment }}</label>
            </div>
            <div class="label-row">
              <label>照片 : </label>
              <label
                ><img :src="data.photo" alt="图片描述" width="400" height="300"
              /></label>
            </div>
            <br />
          </div>
        </form>
        <button
          class="ForeignLanguageDetailsFormCloseButton"
          @click="ForeignLanguageDetailsFormCloseButtonClick"
        >
          关闭
        </button>
      </div>
    </div>

    <div
      v-if="ifPicture"
      class="ForeignLanguageDetailsBackground"
      @click="ForeignLanguageDetailsBackgroundClick"
    >
      <img :src="this.pictureURL" alt="图片描述" width="70%" height="300" />
    </div>
  </div>
</template>

<script>
import axios from "@/axios";

export default {
  data() {
    return {
      ForeignLanguageTable: [],
      ifDetails: false,
      ifPicture: false,
      pictureURL: "",
      studentDetails: {},
      currentPage: 1,
      pageSize: 13,
      total: 130,
    };
  },
  mounted() {
    this.FetchData();
  },
  methods: {
    formatDate(date) {
      const d = new Date(date);
      return `${d.getFullYear()}-${(d.getMonth() + 1)
        .toString()
        .padStart(2, "0")}-${d.getDate().toString().padStart(2, "0")}`;
    },
    ForeignLanguageDetailsBackgroundClick() {
      this.ifPicture = false;
    },
    handlePageChange(page) {
      this.currentPage = page;
      this.FetchData();
    },

    pictureInspectClick(picture) {
      this.pictureURL = picture;
      this.ifPicture = true;
    },

    async FetchData() {
      try {
        const informationResponse = await axios.get("/admin/getEnglishLevel", {
          params: {
            page: this.currentPage,
            pageSize: this.pageSize,
          },
        });
        if (informationResponse.code == 1) {
          this.total = informationResponse.data.total;
          this.ForeignLanguageTable = informationResponse.data.records;
        } else {
          this.$message("数据加载失败!");
        }
      } catch (error) {
        this.$message("数据加载失败!");
        console.error("报错原因：", error);
      }
    },
    ViewDetails(records) {
      this.RenewLabel(records);
      this.ifDetails = true;
    },

    RenewLabel(studentRecord) {
      this.studentDetails = studentRecord.englishExamDetails;
    },
    ForeignLanguageDetailsFormCloseButtonClick() {
      this.ifDetails = false;
    },
  },
};
</script>

<style scoped>
.Details {
  cursor: pointer;
  text-decoration: underline;
}
.Details:hover {
  color: #8491cd; /* 可选：当鼠标悬停时改变颜色 */
}
.ForeignLanguagePagination {
  position: absolute;
  top: 95%;
}
.ForeignLanguageUserNumberInput {
  /* position: absolute; */
  border-radius: 5px;
  outline: none; /* 外边线颜色不要 */
  border: none; /* 边框颜色不要 */
}

.ForeignLanguageTotalBackground {
  position: relative;
  width: 100%;
  height: 100%;
}
.ForeignLanguageTableBackground {
  position: absolute;
  width: 100%;
  height: 90%;
  overflow: auto;
}

@import "/src/components/ManageSystem/TableStyle.css";
@import "/src/components/ManageSystem/DetailsForm/ForeignLanguageForm.css";
@import "/src/components/ManageSystem/MouseScrollWheelStyle/DetailFormMouse.css";
</style>
